import {Anime, CDNCoverImages, CoverImages, UserAnimeEntry, UserMangaEntry} from "../../../../models";
import {
    BRANCH_ANIME,
    BRANCH_EROGE, BRANCH_MANGA,
    CYBER_GENESIS_URL,
    getPriorityLabel,
    USER_ANIME_STATUSES
} from "../../../../globals";
import Grid from "@material-ui/core/Grid";
import Chip from "@material-ui/core/Chip";
import Moment from "react-moment";
import React from "react";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faCalendarAlt, faCheck, faEdit, faImage, faPause, faPlay, faStop} from "@fortawesome/free-solid-svg-icons";
import {CyberButton, CyberLink, PreLoadImage} from "../../../layout/common/common";
import {ANIME_DISPLAY} from "../../../../urls";
import AnimeEntryModalForm from "../../forms/anime/AnimeEntryModalForm";
import UpdateAnimeEpisodeButton from "../../forms/anime/UpdateAnimeEpisodeButton";
import {getTitleCoverImage, isEmpty} from "../../../functions";
import CardUserEditTitleEntry from "../../../layout/cards/CardUserEditTitleEntry";
import {formatTitleForRows} from "../../../layout/rows/functions";
import {formatTitleForUserListRows} from "../functions";


function getTitleAndStatusKeysFromBranch(branch) {
    switch (branch) {
        case BRANCH_ANIME:
            return [UserAnimeEntry.anime, UserAnimeEntry.status]
        case BRANCH_MANGA:
            return [UserMangaEntry.manga, UserMangaEntry.status]
    }
}


/**
 *
 * @param props.selected {String}
 * @param props.setSelected {Function}
 * @returns {JSX.Element}
 * @constructor
 */
export function AnimeListStatusButtons(props) {
    const {selected, setSelected} = props;

    return <>
        <Grid item xs={6} sm={4} md={2} className="status-buttons">
            <CyberButton disabled={true}>All</CyberButton>
        </Grid>
        <Grid item xs={6} sm={4} md={2} className="status-buttons">
            <CyberButton
                classNames={selected === USER_ANIME_STATUSES.watching ? 'btn-contained-success' : ''}
                onClick={() => {
                    setSelected(USER_ANIME_STATUSES.watching)
                }}>
                <FontAwesomeIcon icon={faPlay}/><span className="btn-label">Watching</span>
            </CyberButton>
        </Grid>
        <Grid item xs={6} sm={4} md={2} className="status-buttons">
            <CyberButton
                classNames={selected === USER_ANIME_STATUSES.completed ? 'btn-contained-primary' : ''}
                onClick={() => {
                    setSelected(USER_ANIME_STATUSES.completed)
                }}>
                <FontAwesomeIcon icon={faCheck}/><span className="btn-label">Completed</span>
            </CyberButton>
        </Grid>
        <Grid item xs={6} sm={4} md={2} className="status-buttons">
            <CyberButton
                classNames={selected === USER_ANIME_STATUSES.on_hold ? 'btn-contained-warning' : ''}
                onClick={() => {
                    setSelected(USER_ANIME_STATUSES.on_hold)
                }}>
                <FontAwesomeIcon icon={faPause}/><span className="btn-label">On Hold</span>
            </CyberButton>
        </Grid>
        <Grid item xs={6} sm={4} md={2} className="status-buttons">
            <CyberButton
                classNames={selected === USER_ANIME_STATUSES.dropped ? 'btn-contained-danger' : ''}
                onClick={() => {
                    setSelected(USER_ANIME_STATUSES.dropped)
                }}>
                <FontAwesomeIcon icon={faStop}/><span className="btn-label">Dropped</span>
            </CyberButton>
        </Grid>
        <Grid item xs={6} sm={4} md={2} className="status-buttons">
            <CyberButton
                classNames={selected === USER_ANIME_STATUSES.plan_to_watch ? 'btn-contained-default' : ''}
                onClick={() => {
                    setSelected(USER_ANIME_STATUSES.plan_to_watch)
                }}>
                <FontAwesomeIcon icon={faCalendarAlt}/><span className="btn-label">Planned</span>
            </CyberButton>
        </Grid>
    </>
}


