import React from "react";

import Grid from "@material-ui/core/Grid";
import {
    CheckBoxField,
    ChooseTemplateField,
    ColorPickerField, CSS_FONTS, NumberWithUnitField, SelectWithOptionsField, SimpleTextField
} from "../userListDesignSettingsDialogFields";
import {AboutText, Cards, UserListDesignSettings} from "../user_list_settings_schema";
import InputLabel from "@material-ui/core/InputLabel";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";
import store from "../../../../../store";
import {setUserListDesignSettings} from "../../../../../actions/users";
import OutlinedInput from "@material-ui/core/OutlinedInput";
import InputAdornment from "@material-ui/core/InputAdornment";


/**
 *
 * @param props.branch {String}
 * @param props.settings {Object}
 * @param props.handleChange {Function}
 * @returns {JSX.Element}
 * @constructor
 */
export function GeneralTab(props) {
    const {settings, handleChange} = props;

    return <Grid container className="tab-content-container">
        <Grid className="grid-item" item xs={12}>
            <NumberWithUnitField
                settings={settings}
                handleChange={handleChange}
                label={'Top Margin'}
                html_id_prefix={'list-top-margin'}
                key_1={UserListDesignSettings.list_margin_top}
            />
        </Grid>
        <Grid className="grid-item" item xs={4}>
            <SelectWithOptionsField
                settings={settings}
                handleChange={handleChange}
                label={'Results per Page'}
                html_id_prefix={'list-results-per-page'}
                key_1={UserListDesignSettings.results_per_page}
                options={[15, 25, 50, 100]}
            />
        </Grid>
        <Grid className="grid-item" item xs={4}>
            <CheckBoxField
                settings={settings}
                handleChange={handleChange}
                key_1={UserListDesignSettings.color_dark}
                label={'Dark Text'}
            />
        </Grid>
        {/*<Grid className="grid-item" item xs={12}>*/}
        {/*    <ChooseTemplateField/>*/}
        {/*</Grid>*/}
    </Grid>
}
