import React from "react";

import Chip from "@material-ui/core/Chip";
import {faServer} from "@fortawesome/free-solid-svg-icons";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";

import {BRANCH_ANIME, BRANCH_MANGA} from "../../../globals";
import {Anime, ANIME_SHORT_STATUS, Manga, MANGA_SHORT_STATUS, UserAnimeEntry, UserMangaEntry} from "../../../models";
import {formatContentDateByKnown, userTitlePriorityNumberToText} from "../../functions";
import {ANIME_DISPLAY, MANGA_DISPLAY} from "../../../urls";
import {FilterSchema} from "../../layout/filters/field_names";
import {getUserStatusChip} from "../../layout/cards/functions";
import TableCell from "@material-ui/core/TableCell";
import Moment from "react-moment";
import moment from "moment";
import UpdateAnimeEpisodeButton from "../forms/anime/UpdateAnimeEpisodeButton";
import {UpdateMangaChapterButton, UpdateMangaVolumeButton} from "../forms/manga/UpdateMangaVolumeChapterButtons";


function getDaysFromDate(start_date, finish_date) {
    let start = new Date(start_date)
    let end = new Date()
    if (finish_date)
        end = new Date(finish_date)

    const diff = new moment.duration(end - start);
    return Math.floor(diff.asDays())
}


export function formatTitleForUserListRows(branch, user_status, entry, design_settings, list_owner, list) {
    let start_date = '';
    let end_date = '';
    let card_title = {
        id: 0,
        link: '',
        cover_images: [],
        cdn_cover_images: [],
        title: '',
        chipset_header: [],
        column_set: [],
    };

    switch (branch) {
        case BRANCH_ANIME: {
            const title = entry[UserAnimeEntry.anime]
            start_date = formatContentDateByKnown(title[Anime.air_start], title[Anime.air_start_known], true);
            end_date = formatContentDateByKnown(title[Anime.air_end], title[Anime.air_end_known], true);

            const progress = <>
                {entry[UserAnimeEntry.watched_episodes]}/{title[Anime.episodes] ? title[Anime.episodes] : '-'}{' '}
                {list_owner ? <UpdateAnimeEpisodeButton
                    id={title[Anime.id]} entry={entry}
                    list={list}/> : null}
            </>

            card_title.id = title[Anime.id];
            card_title.link = ANIME_DISPLAY(title[Anime.id]);
            card_title.cover_images = title[Anime.anime_cover_images];
            card_title.cdn_cover_images = title[Anime.anime_cdn_cover_images];
            card_title.title = title[Anime.title];
            try {
                card_title.chipset_header = [{
                    label: ANIME_SHORT_STATUS[title[Anime.status]][FilterSchema.label],
                    color: ANIME_SHORT_STATUS[title[Anime.status]][FilterSchema.color]
                }];
            } catch (e) {
            }
            if (title[Anime.pending_by_MAL])
                card_title.chipset_header.push({label: 'M-PEN', color: 'warning'});
            if (title[Anime.deleted_by_MAL])
                card_title.chipset_header.push({label: 'M-DEL', color: 'danger'});
            if (title[Anime.anime_on_server])
                card_title.chipset_header.push({label: <FontAwesomeIcon icon={faServer}/>, color: 'secondary'});
            if (design_settings.title_status)
                card_title.chipset_header.push(getUserStatusChip(user_status));

            if (design_settings.title_user_score)
                if (entry[UserAnimeEntry.score] !== 0)
                    card_title.column_set.push(design_settings.list_table_text_bubbles ?
                        <Chip variant="default" color="primary" size="small" label={entry[UserAnimeEntry.score]}/> :
                        <span className="row-text row-user-score">{entry[UserAnimeEntry.score]}</span>);
                else card_title.column_set.push(null);

            if (design_settings.title_progress)
                card_title.column_set.push(design_settings.list_table_text_bubbles ?
                    <Chip variant="default" color="primary" size="small" label={progress}/> :
                    <span className="row-text">{progress}</span>);

            if (design_settings.title_priority)
                card_title.column_set.push(design_settings.list_table_text_bubbles ?
                    <Chip variant="default" color="primary" size="small"
                          label={`${userTitlePriorityNumberToText(entry[UserAnimeEntry.priority])}`}/> :
                    <span className="row-text">{userTitlePriorityNumberToText(entry[UserAnimeEntry.priority])}</span>);

            if (design_settings.title_comments)
                if (entry[UserAnimeEntry.comments])
                    card_title.column_set.push(<span className="row-text">{entry[UserAnimeEntry.comments]}</span>);
                else card_title.column_set.push(null);

            if (design_settings.title_tags)
                if (entry[UserAnimeEntry.tags])
                    card_title.column_set.push(<span className="row-text">{entry[UserAnimeEntry.tags]}</span>);
                else card_title.column_set.push(null);

            if (design_settings.title_started)
                if (entry[UserAnimeEntry.start_date])
                    card_title.column_set.push(design_settings.list_table_text_bubbles ?
                        <Chip variant="default" color="primary" size="small"
                              label={<Moment date={entry[UserAnimeEntry.start_date]} format={'Do MMM YYYY'}/>}/> :
                        <span className="row-text">
                            {<Moment date={entry[UserAnimeEntry.start_date]} format={'Do MMM YYYY'}/>}</span>);
                else card_title.column_set.push(null);

            if (design_settings.title_finished)
                if (entry[UserAnimeEntry.finish_date])
                    card_title.column_set.push(design_settings.list_table_text_bubbles ?
                        <Chip variant="default" color="primary" size="small"
                              label={<Moment date={entry[UserAnimeEntry.finish_date]} format={'Do MMM YYYY'}/>}/> :
                        <span className="row-text">
                            {<Moment date={entry[UserAnimeEntry.finish_date]} format={'Do MMM YYYY'}/>}</span>);
                else card_title.column_set.push(null);

            if (design_settings.title_added)
                if (entry[UserAnimeEntry.date_added])
                    card_title.column_set.push(design_settings.list_table_text_bubbles ?
                        <Chip variant="default" color="primary" size="small"
                              label={<Moment date={entry[UserAnimeEntry.date_added]} format={'Do MMM YYYY'}/>}/> :
                        <span className="row-text">
                            {<Moment date={entry[UserAnimeEntry.date_added]} format={'Do MMM YYYY'}/>}</span>);
                else card_title.column_set.push(null);

            if (design_settings.title_days)
                if (entry[UserAnimeEntry.start_date])
                    card_title.column_set.push(design_settings.list_table_text_bubbles ?
                        <Chip variant="default" color="primary" size="small"
                              label={`${getDaysFromDate(
                                  entry[UserAnimeEntry.start_date], entry[UserAnimeEntry.finish_date])} days`}/> :
                        <span className="row-text">{`${getDaysFromDate(
                            entry[UserAnimeEntry.start_date], entry[UserAnimeEntry.finish_date])} days`}</span>);
                else card_title.column_set.push(null);

            if (design_settings.title_type)
                if (title[Anime.type].toLowerCase() !== 'unknown')
                    card_title.column_set.push(design_settings.list_table_text_bubbles ?
                        <Chip variant="default" color="primary" size="small" label={title[Anime.type]}/> :
                        <span className="row-text">{title[Anime.type]}</span>);
                else card_title.column_set.push(null);

            if (design_settings.title_episodes)
                if (title[Anime.episodes])
                    card_title.column_set.push(design_settings.list_table_text_bubbles ?
                        <Chip variant="default" color="primary" size="small" label={`${title[Anime.episodes]}ep`}/> :
                        <span className="row-text">{title[Anime.episodes]}ep</span>);
                else card_title.column_set.push(null);

            if (design_settings.title_mins_per_ep)
                if (title[Anime.minutes_per_episode])
                    card_title.column_set.push(design_settings.list_table_text_bubbles ?
                        <Chip variant="default" color="primary" size="small"
                              label={`${title[Anime.minutes_per_episode]}m`}/> :
                        <span className="row-text">{title[Anime.minutes_per_episode]}m</span>);
                else card_title.column_set.push(null);

            if (design_settings.title_rating)
                if (title[Anime.rating_short])
                    card_title.column_set.push(design_settings.list_table_text_bubbles ?
                        <Chip variant="default" color="primary" size="small" label={title[Anime.rating_short]}/> :
                        <span className="row-text">{title[Anime.rating_short]}</span>);
                else card_title.column_set.push(null);

            if (design_settings.title_genres)
                if (title[Anime.genres_csv]) {
                    let genres;
                    if (design_settings.list_table_text_bubbles) {
                        genres = [];
                        title[Anime.genres_csv].split(', ').map(genre => (
                            genres.push(<Chip variant="default" color="primary" size="small" label={genre}/>)
                        ));
                    } else genres = <span className="row-text">{title[Anime.genres_csv]}</span>
                    card_title.column_set.push(genres);
                }

            if (design_settings.title_mal_score)
                if (title[Anime.score])
                    card_title.column_set.push(design_settings.list_table_text_bubbles ?
                        <Chip variant="default" color="primary" size="small" label={title[Anime.score]}/> :
                        <span className="row-text">{title[Anime.score]}</span>);
                else card_title.column_set.push(null);

            if (design_settings.title_aired)
                if (start_date)
                    card_title.column_set.push(design_settings.list_table_text_bubbles ?
                        <Chip variant="default" color="primary" size="small"
                              label={`${start_date}${end_date ? ' to ' + end_date : ''}`}/> :
                        <span className="row-text">{start_date}{end_date ? ' to ' + end_date : ''}</span>);
                else card_title.column_set.push(null);

            return card_title;
        }
        case BRANCH_MANGA: {
            const title = entry[UserMangaEntry.manga]
            start_date = formatContentDateByKnown(title[Manga.air_start], title[Manga.air_start_known], true);
            end_date = formatContentDateByKnown(title[Manga.air_end], title[Manga.air_end_known], true);

            const progress_volumes = <>
                {entry[UserMangaEntry.read_volumes]}/{title[Manga.volumes] ? title[Manga.volumes] : '-'}{' '}
                {list_owner ? <UpdateMangaVolumeButton
                    id={title[Manga.id]} entry={entry}
                    list={list}/> : null}
            </>
            const progress_chapters = <>
                {entry[UserMangaEntry.read_chapters]}/{title[Manga.chapters] ? title[Manga.chapters] : '-'}{' '}
                {list_owner ? <UpdateMangaChapterButton
                    id={title[Manga.id]} entry={entry}
                    list={list}/> : null}
            </>

            card_title.id = title[Manga.id];
            card_title.link = MANGA_DISPLAY(title[Manga.id]);
            card_title.cover_images = title[Manga.manga_cover_images];
            card_title.cdn_cover_images = title[Manga.manga_cdn_cover_images];
            card_title.title = title[Manga.title];
            try {
                card_title.chipset_header = [{
                    label: MANGA_SHORT_STATUS[title[Manga.status]][FilterSchema.label],
                    color: MANGA_SHORT_STATUS[title[Manga.status]][FilterSchema.color]
                }];
            } catch (e) {
            }
            if (title[Manga.pending_by_MAL])
                card_title.chipset_header.push({label: 'M-PEN', color: 'warning'});
            if (title[Manga.deleted_by_MAL])
                card_title.chipset_header.push({label: 'M-DEL', color: 'danger'});
            if (title[Manga.manga_on_server])
                card_title.chipset_header.push({label: <FontAwesomeIcon icon={faServer}/>, color: 'secondary'});
            if (user_status)
                card_title.chipset_header.push(getUserStatusChip(user_status));

            if (design_settings.title_user_score)
                if (entry[UserMangaEntry.score] !== 0)
                    card_title.column_set.push(
                        <Chip variant="default" color="primary" size="small" label={entry[UserMangaEntry.score]}/>);
                else card_title.column_set.push(null);

            if (design_settings.title_progress)
                card_title.column_set.push(
                    <Chip variant="default" color="primary" size="small" label={progress_volumes}/>);

            if (design_settings.title_progress_2)
                card_title.column_set.push(
                    <Chip variant="default" color="primary" size="small" label={progress_chapters}/>);

            if (design_settings.title_priority)
                card_title.column_set.push(
                    <Chip variant="default" color="primary" size="small"
                          label={`${userTitlePriorityNumberToText(entry[UserMangaEntry.priority])}`}/>);

            if (design_settings.title_comments)
                if (entry[UserMangaEntry.comments])
                    card_title.column_set.push(<p>{entry[UserMangaEntry.comments]}</p>);
                else card_title.column_set.push(null);

            if (design_settings.title_tags)
                if (entry[UserMangaEntry.tags])
                    card_title.column_set.push(<p>{entry[UserMangaEntry.tags]}</p>);
                else card_title.column_set.push(null);

            if (design_settings.title_started)
                if (entry[UserMangaEntry.start_date])
                    card_title.column_set.push(
                        <Chip variant="default" color="primary" size="small"
                              label={<Moment date={entry[UserMangaEntry.start_date]} format={'Do MMM YYYY'}/>}/>);
                else card_title.column_set.push(null);

            if (design_settings.title_finished)
                if (entry[UserMangaEntry.finish_date])
                    card_title.column_set.push(
                        <Chip variant="default" color="primary" size="small"
                              label={<Moment date={entry[UserMangaEntry.finish_date]} format={'Do MMM YYYY'}/>}/>);
                else card_title.column_set.push(null);

            if (design_settings.title_added)
                if (entry[UserMangaEntry.date_added])
                    card_title.column_set.push(
                        <Chip variant="default" color="primary" size="small"
                              label={<Moment date={entry[UserMangaEntry.date_added]} format={'Do MMM YYYY'}/>}/>);
                else card_title.column_set.push(null);

            if (design_settings.title_days)
                if (entry[UserMangaEntry.start_date])
                    card_title.column_set.push(
                        <Chip variant="default" color="primary" size="small"
                              label={`${getDaysFromDate(
                                  entry[UserMangaEntry.start_date], entry[UserMangaEntry.finish_date])} days`}/>);
                else card_title.column_set.push(null);

            if (design_settings.title_type)
                if (title[Manga.type].toLowerCase() !== 'unknown')
                    card_title.column_set.push(
                        <Chip variant="default" color="primary" size="small" label={title[Manga.type]}/>);
                else card_title.column_set.push(null);

            if (design_settings.title_volumes)
                if (title[Manga.volumes])
                    card_title.column_set.push(
                        <Chip variant="default" color="primary" size="small" label={`${title[Manga.volumes]}v`}/>);
                else card_title.column_set.push(null);

            if (design_settings.title_chapters)
                if (title[Manga.chapters])
                    card_title.column_set.push(
                        <Chip variant="default" color="primary" size="small" label={`${title[Manga.chapters]}c`}/>);
                else card_title.column_set.push(null);

            if (design_settings.title_genres)
                if (title[Manga.genres_csv]) {
                    let genres = [];
                    title[Manga.genres_csv].split(', ').map(genre => (
                        genres.push(<Chip variant="default" color="primary" size="small" label={genre}/>)
                    ));
                    card_title.column_set.push(genres);
                }

            if (design_settings.title_mal_score)
                if (title[Manga.score])
                    card_title.column_set.push(
                        <Chip variant="default" color="primary" size="small" label={title[Manga.score]}/>);
                else card_title.column_set.push(null);

            if (design_settings.title_aired)
                if (start_date)
                    card_title.column_set.push(
                        <Chip variant="default" color="primary" size="small"
                              label={`${start_date}${end_date ? ' to ' + end_date : ''}`}/>);
                else card_title.column_set.push(null);

            return card_title;
        }
        default:
            return card_title
    }
}
