import React, {useState} from 'react'

import {CyberButton} from "../../../layout/common/common";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faEdit} from "@fortawesome/free-solid-svg-icons";
import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import {Profile, User} from "../../../../models";
import {setUserListDesignSettings} from "../../../../actions/users";
import store from "../../../../store";
import {BRANCH_ANIME, BRANCH_MANGA} from "../../../../globals";
import {UserListDesignSettingsDialogTabs} from "./tabs/userListDesignSettingsDialogTabs";


/**
 *
 * @param props.branch
 * @param props.user_settings
 * @returns {JSX.Element}
 * @constructor
 */
export function UserListDesignSettingsDialog(props) {
    let user_settings = {};
    Object.assign(user_settings, props.user_settings);

    const [open, setOpen] = useState(false);
    const [settings, setSettings] = useState(user_settings);

    const handleOpen = () => {
        setSettings(user_settings);
        setOpen(true);
    };

    const handleClose = (reset = true) => {
        if (reset) {
            let previous_settings = {};
            if (props.branch === BRANCH_ANIME)
                Object.assign(previous_settings, store.getState().auth.user[User.profile][Profile.user_anime_list_design_settings]);
            else if (props.branch === BRANCH_MANGA)
                Object.assign(previous_settings, store.getState().auth.user[User.profile][Profile.user_manga_list_design_settings]);
            store.dispatch(setUserListDesignSettings(props.branch, previous_settings));
        }
        setOpen(false);
    };

    const handleChange = (value, key_1, key_2) => {
        if (key_2)
            setSettings((prevSettings) => {
                store.dispatch(setUserListDesignSettings(props.branch, {
                    ...prevSettings,
                    [key_1]: {...prevSettings[key_1], [key_2]: value}
                }));
                return {...prevSettings, [key_1]: {...prevSettings[key_1], [key_2]: value}}
            });
        else
            setSettings((prevSettings) => {
                store.dispatch(setUserListDesignSettings(props.branch, {...prevSettings, [key_1]: value}));
                return {...prevSettings, [key_1]: value}
            });
    };

    return <div className="user-list-design-settings-dialog-container">
        <CyberButton classNames="user-edit-list-btn btn-contained-primary" onClick={handleOpen}
                     variant="contained" title={`Edit ${props.branch} list design`}>
            <FontAwesomeIcon icon={faEdit}/>
        </CyberButton>
        <Dialog
            open={open}
            onClose={handleClose}
            id="User-List-Design-Settings-Dialog"
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
        >
            <DialogTitle>User {props.branch} list settings</DialogTitle>
            <DialogContent className="settings-wrapper">
                <UserListDesignSettingsDialogTabs branch={props.branch} settings={settings} handleChange={handleChange}/>
            </DialogContent>
            <DialogActions>
                <CyberButton variant="outlined" title={'Save settings'} onClick={() => {
                    store.dispatch(setUserListDesignSettings(props.branch, settings, true));
                    handleClose(false);
                }}>
                    Save
                </CyberButton>
            </DialogActions>
        </Dialog>
    </div>
}
