import React from 'react'

import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faCog, faThLarge, faThList} from "@fortawesome/free-solid-svg-icons";
import Checkbox from "@material-ui/core/Checkbox";

import {BRANCH_ANIME, BRANCH_EROGE, BRANCH_MANGA} from "../../../globals";
import store from "../../../store";
import {
    Profile,
    User,
    UserAnimeDisplaySettingsSchema,
    UserErogeDisplaySettingsSchema,
    UserMangaDisplaySettingsSchema
} from "../../../models";
import {CyberButton} from "../common/common";
import {setUserTitleDisplaySettings} from "../../../actions/users";


/**
 *
 * @param props.branch
 * @param props.user_display_settings
 * @returns {JSX.Element}
 * @constructor
 */
export default function UserDisplaySettingsDialog(props) {
    switch (props.branch) {
        case BRANCH_ANIME:
            return <UserBrowseAnimeDisplaySettingsDialog user_display_settings={props.user_display_settings}/>;
        case BRANCH_MANGA:
            return <UserBrowseMangaDisplaySettingsDialog user_display_settings={props.user_display_settings}/>;
        case BRANCH_EROGE:
            return <UserBrowseErogeDisplaySettingsDialog user_display_settings={props.user_display_settings}/>;
        default:
            return null
    }
}


/**
 *
 * @param props.user_display_settings
 * @returns {JSX.Element}
 * @constructor
 */
function UserBrowseAnimeDisplaySettingsDialog(props) {
    let settings = {};
    Object.assign(settings, props.user_display_settings);

    const [open, setOpen] = React.useState(false);
    const [cards, setCards] = React.useState(settings[UserAnimeDisplaySettingsSchema.cards]);
    const [type, setType] = React.useState(settings[UserAnimeDisplaySettingsSchema.type]);
    const [episodes, setEpisodes] = React.useState(settings[UserAnimeDisplaySettingsSchema.episodes]);
    const [mins_per_ep, setMinsPerEp] = React.useState(settings[UserAnimeDisplaySettingsSchema.mins_per_ep]);
    const [rating, setRating] = React.useState(settings[UserAnimeDisplaySettingsSchema.rating]);
    const [genres, setGenres] = React.useState(settings[UserAnimeDisplaySettingsSchema.genres]);
    const [mal_score, setMalScore] = React.useState(settings[UserAnimeDisplaySettingsSchema.mal_score]);
    const [aired, setAired] = React.useState(settings[UserAnimeDisplaySettingsSchema.aired]);

    const handleClickOpen = () => {
        setCards(settings[UserAnimeDisplaySettingsSchema.cards]);
        setType(settings[UserAnimeDisplaySettingsSchema.type]);
        setEpisodes(settings[UserAnimeDisplaySettingsSchema.episodes]);
        setMinsPerEp(settings[UserAnimeDisplaySettingsSchema.mins_per_ep]);
        setRating(settings[UserAnimeDisplaySettingsSchema.rating]);
        setGenres(settings[UserAnimeDisplaySettingsSchema.genres]);
        setMalScore(settings[UserAnimeDisplaySettingsSchema.mal_score]);
        setAired(settings[UserAnimeDisplaySettingsSchema.aired]);
        setOpen(true);
    };

    const handleClose = (reset= true) => {
        if (reset) {
            let previous_settings = {};
            Object.assign(previous_settings, store.getState().auth.user[User.profile][Profile.user_anime_display_settings]);
            store.dispatch(setUserTitleDisplaySettings(BRANCH_ANIME, previous_settings));
        }
        setOpen(false);
    };

    const handleCards = () => {
        setCards(!cards);
        settings[UserAnimeDisplaySettingsSchema.cards] = !cards;
        store.dispatch(setUserTitleDisplaySettings(BRANCH_ANIME, settings));
    };

    const handleType = (e) => {
        setType(e.target.checked);
        settings[UserAnimeDisplaySettingsSchema.type] = e.target.checked;
        store.dispatch(setUserTitleDisplaySettings(BRANCH_ANIME, settings));
    };

    const handleEpisodes = (e) => {
        setEpisodes(e.target.checked);
        settings[UserAnimeDisplaySettingsSchema.episodes] = e.target.checked;
        store.dispatch(setUserTitleDisplaySettings(BRANCH_ANIME, settings));
    };

    const handleMinsPerEp = (e) => {
        setMinsPerEp(e.target.checked);
        settings[UserAnimeDisplaySettingsSchema.mins_per_ep] = e.target.checked;
        store.dispatch(setUserTitleDisplaySettings(BRANCH_ANIME, settings));
    };

    const handleRating = (e) => {
        setRating(e.target.checked);
        settings[UserAnimeDisplaySettingsSchema.rating] = e.target.checked;
        store.dispatch(setUserTitleDisplaySettings(BRANCH_ANIME, settings));
    };

    const handleGenres = (e) => {
        setGenres(e.target.checked);
        settings[UserAnimeDisplaySettingsSchema.genres] = e.target.checked;
        store.dispatch(setUserTitleDisplaySettings(BRANCH_ANIME, settings));
    };

    const handleMalScore = (e) => {
        setMalScore(e.target.checked);
        settings[UserAnimeDisplaySettingsSchema.mal_score] = e.target.checked;
        store.dispatch(setUserTitleDisplaySettings(BRANCH_ANIME, settings));
    };

    const handleAired = (e) => {
        setAired(e.target.checked);
        settings[UserAnimeDisplaySettingsSchema.aired] = e.target.checked;
        store.dispatch(setUserTitleDisplaySettings(BRANCH_ANIME, settings));
    };

    return <div className="user-browse-display-settings-dialog-container">
        <CyberButton variant="outlined" onClick={handleClickOpen} title={'Display settings'}>
            <FontAwesomeIcon icon={faCog}/>
        </CyberButton>
        <Dialog
            open={open}
            onClose={handleClose}
            id="User-Browse-Display-Settings-Dialog"
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
        >
            <DialogTitle>User settings</DialogTitle>
            <DialogContent className="settings-wrapper">
                <table>
                    <tbody>
                    <tr>
                        <td><p>Titles</p></td>
                        <td className="text-center">
                            <CyberButton classNames="btn-contained-primary" variant="contained"
                                         onClick={handleCards}>
                                <FontAwesomeIcon
                                    icon={cards ? faThList : faThLarge}
                                    title={cards ? 'Table' : 'Cards'}
                                />
                            </CyberButton>
                        </td>
                        <td><p>Type</p></td>
                        <td>
                            <Checkbox
                                checked={type}
                                onChange={handleType}
                                inputProps={{ 'aria-label': 'primary checkbox' }}
                              />
                        </td>
                    </tr>
                    <tr>
                        <td><p>Episodes</p></td>
                        <td>
                            <Checkbox
                                checked={episodes}
                                onChange={handleEpisodes}
                                inputProps={{ 'aria-label': 'primary checkbox' }}
                              />
                        </td>
                        <td><p>Mins. per episode</p></td>
                        <td>
                            <Checkbox
                                checked={mins_per_ep}
                                onChange={handleMinsPerEp}
                                inputProps={{ 'aria-label': 'primary checkbox' }}
                              />
                        </td>
                    </tr>
                    <tr>
                        <td><p>Rating</p></td>
                        <td>
                            <Checkbox
                                checked={rating}
                                onChange={handleRating}
                                inputProps={{ 'aria-label': 'primary checkbox' }}
                              />
                        </td>
                        <td><p>Genres</p></td>
                        <td>
                            <Checkbox
                                checked={genres}
                                onChange={handleGenres}
                                inputProps={{ 'aria-label': 'primary checkbox' }}
                              />
                        </td>
                    </tr>
                    <tr>
                        <td><p>MAL Score</p></td>
                        <td>
                            <Checkbox
                                checked={mal_score}
                                onChange={handleMalScore}
                                inputProps={{ 'aria-label': 'primary checkbox' }}
                              />
                        </td>
                        <td><p>Aired</p></td>
                        <td>
                            <Checkbox
                                checked={aired}
                                onChange={handleAired}
                                inputProps={{ 'aria-label': 'primary checkbox' }}
                              />
                        </td>
                    </tr>
                    </tbody>
                </table>
            </DialogContent>
            <DialogActions>
                <CyberButton variant="outlined" title={'Save settings'} onClick={() => {
                    store.dispatch(setUserTitleDisplaySettings(BRANCH_ANIME, settings, true));
                    handleClose(false);
                }}>
                    Save
                </CyberButton>
            </DialogActions>
        </Dialog>
    </div>
}


