import {BRANCH_ANIME, BRANCH_CHARACTERS, BRANCH_EROGE, BRANCH_MANGA, BRANCH_PEOPLE} from "../../../globals";
import store from "../../../store";
import {formatContentDateByKnown} from "../../functions";
import {
    Anime,
    ANIME_SHORT_STATUS, Character, Eroge,
    Manga,
    MANGA_SHORT_STATUS, Person,
    UserAnimeDisplaySettingsSchema, UserErogeDisplaySettingsSchema,
    UserMangaDisplaySettingsSchema
} from "../../../models";
import {ANIME_DISPLAY, CHARACTERS_DISPLAY, EROGE_DISPLAY, MANGA_DISPLAY, PERSON_DISPLAY} from "../../../urls";
import {FilterSchema} from "../filters/field_names";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faServer} from "@fortawesome/free-solid-svg-icons";
import React from "react";
import {getUserStatusChip} from "../cards/functions";
import Chip from "@material-ui/core/Chip";


export function formatTitleForRows(branch, title, user_status) {
    let start_date = '';
    let end_date = '';
    let card_title = {
        id: 0,
        link: '',
        cover_images: [],
        cdn_cover_images: [],
        title: '',
        chipset_header: [],
        column_set: [],
    };
    let user_settings = {};

    switch (branch) {
        case BRANCH_ANIME:
            user_settings = store.getState().users.user_anime_display_settings;
            start_date = formatContentDateByKnown(title[Anime.air_start], title[Anime.air_start_known], true);
            end_date = formatContentDateByKnown(title[Anime.air_end], title[Anime.air_end_known], true);

            card_title.id = title[Anime.id];
            card_title.link = ANIME_DISPLAY(title[Anime.id]);
            card_title.cover_images = title[Anime.anime_cover_images];
            card_title.cdn_cover_images = title[Anime.anime_cdn_cover_images];
            card_title.title = title[Anime.title];
            try {
                card_title.chipset_header = [{
                    label: ANIME_SHORT_STATUS[title[Anime.status]][FilterSchema.label],
                    color: ANIME_SHORT_STATUS[title[Anime.status]][FilterSchema.color]
                }];
            } catch (e) {
            }
            if (title[Anime.pending_by_MAL])
                card_title.chipset_header.push({label: 'M-PEN', color: 'warning'});
            if (title[Anime.deleted_by_MAL])
                card_title.chipset_header.push({label: 'M-DEL', color: 'danger'});
            if (title[Anime.anime_on_server])
                card_title.chipset_header.push({label: <FontAwesomeIcon icon={faServer}/>, color: 'secondary'});
            if (user_status)
                card_title.chipset_header.push(getUserStatusChip(user_status));

            if (user_settings[UserAnimeDisplaySettingsSchema.type])
                if (title[Anime.type].toLowerCase() !== 'unknown')
                    card_title.column_set.push(
                        <Chip variant="default" color="primary" size="small" label={title[Anime.type]}/>);
                else card_title.column_set.push(null);

            if (user_settings[UserAnimeDisplaySettingsSchema.episodes])
                if (title[Anime.episodes])
                    card_title.column_set.push(
                        <Chip variant="default" color="primary" size="small" label={`${title[Anime.episodes]}ep`}/>);
                else card_title.column_set.push(null);

            if (user_settings[UserAnimeDisplaySettingsSchema.mins_per_ep])
                if (title[Anime.minutes_per_episode])
                    card_title.column_set.push(
                        <Chip variant="default" color="primary" size="small"
                              label={`${title[Anime.minutes_per_episode]}m`}/>);
                else card_title.column_set.push(null);

            if (user_settings[UserAnimeDisplaySettingsSchema.rating])
                if (title[Anime.rating_short])
                    card_title.column_set.push(
                        <Chip variant="default" color="primary" size="small" label={title[Anime.rating_short]}/>);
                else card_title.column_set.push(null);

            if (user_settings[UserAnimeDisplaySettingsSchema.genres])
                if (title[Anime.genres_csv]) {
                    let genres = [];
                    title[Anime.genres_csv].split(', ').map(genre => (
                        genres.push(<Chip variant="default" color="primary" size="small" label={genre}/>)
                    ));
                    card_title.column_set.push(genres);
                }

            if (user_settings[UserAnimeDisplaySettingsSchema.mal_score])
                if (title[Anime.score])
                    card_title.column_set.push(
                        <Chip variant="default" color="primary" size="small" label={title[Anime.score]}/>);
                else card_title.column_set.push(null);

            if (user_settings[UserAnimeDisplaySettingsSchema.aired])
                if (start_date)
                    card_title.column_set.push(
                        <Chip variant="default" color="primary" size="small"
                              label={`${start_date}${end_date ? ' to ' + end_date : ''}`}/>);
                else card_title.column_set.push(null);

            return card_title;
        case BRANCH_MANGA:
            user_settings = store.getState().users.user_manga_display_settings;
            start_date = formatContentDateByKnown(title[Manga.air_start], title[Manga.air_start_known], true);
            end_date = formatContentDateByKnown(title[Manga.air_end], title[Manga.air_end_known], true);

            card_title.id = title[Manga.id];
            card_title.link = MANGA_DISPLAY(title[Manga.id]);
            card_title.cover_images = title[Manga.manga_cover_images];
            card_title.cdn_cover_images = title[Manga.manga_cdn_cover_images];
            card_title.title = title[Manga.title];
            try {
                card_title.chipset_header = [{
                    label: MANGA_SHORT_STATUS[title[Manga.status]][FilterSchema.label],
                    color: MANGA_SHORT_STATUS[title[Manga.status]][FilterSchema.color]
                }];
            } catch (e) {
            }
            if (title[Manga.pending_by_MAL])
                card_title.chipset_header.push({label: 'M-PEN', color: 'warning'});
            if (title[Manga.deleted_by_MAL])
                card_title.chipset_header.push({label: 'M-DEL', color: 'danger'});
            if (title[Manga.manga_on_server])
                card_title.chipset_header.push({label: <FontAwesomeIcon icon={faServer}/>, color: 'secondary'});
            if (user_status)
                card_title.chipset_header.push(getUserStatusChip(user_status));

            if (user_settings[UserMangaDisplaySettingsSchema.type])
                if (title[Manga.type].toLowerCase() !== 'unknown')
                    card_title.column_set.push(
                        <Chip variant="default" color="primary" size="small" label={title[Manga.type]}/>);
                else card_title.column_set.push(null);

            if (user_settings[UserMangaDisplaySettingsSchema.volumes])
                if (title[Manga.volumes])
                    card_title.column_set.push(
                        <Chip variant="default" color="primary" size="small" label={`${title[Manga.volumes]}v`}/>);
                else card_title.column_set.push(null);

            if (user_settings[UserMangaDisplaySettingsSchema.chapters])
                if (title[Manga.chapters])
                    card_title.column_set.push(
                        <Chip variant="default" color="primary" size="small" label={`${title[Manga.chapters]}c`}/>);
                else card_title.column_set.push(null);

            if (user_settings[UserMangaDisplaySettingsSchema.genres])
                if (title[Manga.genres_csv]) {
                    let genres = [];
                    title[Manga.genres_csv].split(', ').map(genre => (
                        genres.push(<Chip variant="default" color="primary" size="small" label={genre}/>)
                    ));
                    card_title.column_set.push(genres);
                }

            if (user_settings[UserMangaDisplaySettingsSchema.mal_score])
                if (title[Manga.score])
                    card_title.column_set.push(
                        <Chip variant="default" color="primary" size="small" label={title[Manga.score]}/>);
                else card_title.column_set.push(null);

            if (user_settings[UserMangaDisplaySettingsSchema.aired])
                if (start_date)
                    card_title.column_set.push(
                        <Chip variant="default" color="primary" size="small"
                              label={`${start_date}${end_date ? ' to ' + end_date : ''}`}/>);
                else card_title.column_set.push(null);

            return card_title;
        case BRANCH_CHARACTERS:
            card_title.id = title[Character.id];
            card_title.link = CHARACTERS_DISPLAY(title[Character.id]);
            card_title.cover_images = title[Character.character_cover_images];
            card_title.cdn_cover_images = title[Character.character_cdn_cover_images];
            card_title.title = title[Character.short_name] ? title[Character.short_name] : title[Character.character_name];
            return card_title;
        case BRANCH_PEOPLE:
            card_title.id = title[Person.id];
            card_title.link = PERSON_DISPLAY(title[Person.id]);
            card_title.cover_images = title[Person.person_cover_images];
            card_title.cdn_cover_images = title[Person.person_cdn_cover_images];
            card_title.title = title[Person.person_name];
            return card_title;
        case BRANCH_EROGE:
            user_settings = store.getState().users.user_eroge_display_settings;
            card_title.id = title[Eroge.id];
            card_title.link = EROGE_DISPLAY(title[Eroge.id]);
            card_title.cover_images = title[Eroge.eroge_cover_images];
            card_title.title = title[Eroge.eroge_name].replace('[Android]', '').trim();
            if (title[Eroge.downloaded])
                card_title.chipset_header.push({label: <FontAwesomeIcon icon={faServer}/>, color: 'secondary'});

            if (user_settings[UserErogeDisplaySettingsSchema.type])
                if (title[Eroge.type].toLowerCase() !== 'unknown')
                    card_title.column_set.push(
                        <Chip variant="default" color="primary" size="small" label={title[Eroge.type]}/>);
                else card_title.column_set.push(null);

            if (user_settings[UserErogeDisplaySettingsSchema.censorship])
                if (title[Eroge.censorship].toLowerCase() !== 'unknown')
                    card_title.column_set.push(
                        <Chip variant="default" color="primary" size="small" label={title[Eroge.censorship]}/>);
                else card_title.column_set.push(null);

            if (user_settings[UserErogeDisplaySettingsSchema.language])
                if (title[Eroge.language].toLowerCase() !== 'unknown')
                    card_title.column_set.push(
                        <Chip variant="default" color="primary" size="small" label={title[Eroge.language]}/>);
                else card_title.column_set.push(null);

            if (user_settings[UserErogeDisplaySettingsSchema.platforms])
                if (title[Eroge.platforms].toLowerCase() !== 'unknown')
                    card_title.column_set.push(
                        <Chip variant="default" color="primary" size="small" label={title[Eroge.platforms]}/>);
                else card_title.column_set.push(null);

            if (user_settings[UserErogeDisplaySettingsSchema.tags])
                if (title[Eroge.tags]) {
                    let genres = [];
                    title[Eroge.tags].split(', ').map(genre => (
                        genres.push(<Chip variant="default" color="primary" size="small" label={genre}/>)
                    ));
                    card_title.column_set.push(genres);
                }

            if (user_settings[UserErogeDisplaySettingsSchema.hg_score])
                if (title[Eroge.rating])
                    card_title.column_set.push(
                        <Chip variant="default" color="primary" size="small" label={title[Eroge.rating]}/>);
                else card_title.column_set.push(null);

            return card_title;
        default:
            return card_title
    }
}
