import React from 'react';
import PropTypes from 'prop-types';

import SwipeableViews from 'react-swipeable-views';
import {makeStyles, useTheme} from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import {playClickSound, playHoverSound} from "../../../../layout/sounds";
import {GeneralTab} from "./GeneralTab";
import {CoverImgTab} from "./CoverImgTab";
import {BackgroundImgTab} from "./BackgroundImgTab";
import {MessageTab} from "./MessageTab";
import {ListTab} from "./ListTab";
import {OverlayImgTab} from "./OverlayImgTab";


function TabPanel(props) {
    const {children, value, index, ...other} = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`full-width-tabpanel-${index}`}
            aria-labelledby={`full-width-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box p={3}>
                    <Typography>{children}</Typography>
                </Box>
            )}
        </div>
    );
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired,
};

function a11yProps(index) {
    return {
        id: `full-width-tab-${index}`,
        'aria-controls': `full-width-tabpanel-${index}`,
    };
}

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
}));

/**
 * Material UI tabs. Supply it with tab information to render the tabs.
 * @param props.branch {String}
 * @param props.settings {Object}
 * @param props.handleChange {Function}
 * @returns {JSX.Element}
 * @constructor
 */
export function UserListDesignSettingsDialogTabs(props) {
    const {settings, handleChange} = props;
    const classes = useStyles();
    const theme = useTheme();
    const [value, setValue] = React.useState(0);

    const handleTabChange = (event, newValue) => {
        setValue(newValue);
    };

    const handleChangeIndex = (index) => {
        setValue(index);
    };

    return <div className={classes.root + ' tabs-root'}>
        <AppBar position="static" color="primary" className="cyber-tabs">
            <Tabs
                value={value}
                onChange={handleTabChange}
                indicatorColor="secondary"
                textColor="default"
                variant="scrollable"
                scrollButtons="auto"
            >
                <Tab label="General" {...a11yProps(0)} onClick={playClickSound} onMouseEnter={playHoverSound}/>
                <Tab label="Message" {...a11yProps(1)} onClick={playClickSound} onMouseEnter={playHoverSound}/>
                <Tab label="Cover Img" {...a11yProps(2)} onClick={playClickSound} onMouseEnter={playHoverSound}/>
                <Tab label="Overlay Img" {...a11yProps(3)} onClick={playClickSound} onMouseEnter={playHoverSound}/>
                <Tab label="Bg Img" {...a11yProps(4)} onClick={playClickSound} onMouseEnter={playHoverSound}/>
                <Tab label="List" {...a11yProps(5)} onClick={playClickSound} onMouseEnter={playHoverSound}/>
            </Tabs>
        </AppBar>
        <SwipeableViews
            axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
            index={value}
            onChangeIndex={handleChangeIndex}
        >
            <TabPanel value={value} index={0} dir={theme.direction}>
                <GeneralTab branch={props.branch} settings={settings} handleChange={handleChange}/>
            </TabPanel>
            <TabPanel value={value} index={1} dir={theme.direction}>
                <MessageTab settings={settings} handleChange={handleChange}/>
            </TabPanel>
            <TabPanel value={value} index={2} dir={theme.direction}>
                <CoverImgTab settings={settings} handleChange={handleChange}/>
            </TabPanel>
            <TabPanel value={value} index={3} dir={theme.direction}>
                <OverlayImgTab settings={settings} handleChange={handleChange}/>
            </TabPanel>
            <TabPanel value={value} index={4} dir={theme.direction}>
                <BackgroundImgTab settings={settings} handleChange={handleChange}/>
            </TabPanel>
            <TabPanel value={value} index={5} dir={theme.direction}>
                <ListTab branch={props.branch} settings={settings} handleChange={handleChange}/>
            </TabPanel>
        </SwipeableViews>
    </div>
}