import {
    ApiResult,
    UserAnimeDisplaySettingsSchema,
    UserErogeDisplaySettingsSchema,
    UserMangaDisplaySettingsSchema
} from "../../models";
import Grow from "@material-ui/core/Grow";
import GridListTile from "@material-ui/core/GridListTile";
import TitleCard from "../layout/cards/TitleCard";
import {getTitleCoverImage, isEmpty} from "../functions";
import GridList from "@material-ui/core/GridList";
import React from "react";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faEdit, faImage} from "@fortawesome/free-solid-svg-icons";
import TableBody from "@material-ui/core/TableBody";
import TableContainer from "@material-ui/core/TableContainer";
import TitleRow from "../layout/rows/TitleRow";
import {BRANCH_ANIME, BRANCH_EROGE, BRANCH_MANGA} from "../../globals";


/**
 *
 * @param props.gridlist_cols
 * @param props.filtered_titles
 * @param props.branch
 * @param props.current_page
 * @param props.items_per_page
 * @param props.user_entry_statuses
 * @returns {JSX.Element}
 */
export function BrowseCardsView(props) {
    return <GridList className="browse-cards-view" cellHeight={400} cols={props.gridlist_cols} style={{margin: '0 0 10px 0'}}>
        {props.filtered_titles[ApiResult.results].map((title, i) => (
            <Grow key={i} in {...({timeout: 200 + (i * 50)})}>
                <GridListTile cols={1}>
                    <TitleCard
                        branch={props.branch}
                        title={title}
                        user_buttons={true}
                        index={i + ((props.current_page * props.items_per_page) - (props.items_per_page - 1))}
                        user_status={!isEmpty(props.user_entry_statuses) ?
                            props.user_entry_statuses[`${title['id']}`] : null}
                    />
                </GridListTile>
            </Grow>
        ))}
    </GridList>
}


/**
 *
 * @param props.filtered_titles
 * @param props.branch
 * @param props.current_page
 * @param props.items_per_page
 * @param props.user_entry_statuses
 * @param props.user_display_settings
 * @returns {JSX.Element}
 * @constructor
 */
export function BrowseTableView(props) {
    return <TableContainer className="browse-table-view">
        <Table aria-label="user list table">
            <BrowseTableHead branch={props.branch} user_display_settings={props.user_display_settings}/>
            <TableBody>
                {props.filtered_titles[ApiResult.results].map((title, i) => (
                    <TitleRow
                        key={i}
                        branch={props.branch}
                        title={title}
                        user_buttons={true}
                        index={i + ((props.current_page * props.items_per_page) - (props.items_per_page - 1))}
                        user_status={!isEmpty(props.user_entry_statuses) ?
                            props.user_entry_statuses[`${title['id']}`] : null}
                    />
                ))}
            </TableBody>
        </Table>
    </TableContainer>
}


/**
 *
 * @param props.branch
 * @param props.user_display_settings
 * @returns {JSX.Element}
 * @constructor
 */
export function BrowseTableHead(props) {
    switch (props.branch) {
        case BRANCH_ANIME:
            return <TableHead>
                <TableRow>
                    <TableCell className="color-gray" align="center">#</TableCell>
                    <TableCell className="color-gray" align="center"><FontAwesomeIcon icon={faImage}/></TableCell>
                    <TableCell className="color-gray">Title</TableCell>
                    <TableCell className="color-gray" align="center"><FontAwesomeIcon icon={faEdit}/></TableCell>
                    {props.user_display_settings[UserAnimeDisplaySettingsSchema.type] ?
                        <TableCell className="color-gray" align="center">Type</TableCell> : null}
                    {props.user_display_settings[UserAnimeDisplaySettingsSchema.episodes] ?
                        <TableCell className="color-gray" align="center">Episodes</TableCell> : null}
                    {props.user_display_settings[UserAnimeDisplaySettingsSchema.mins_per_ep] ?
                        <TableCell className="color-gray" align="center">Min per ep</TableCell> : null}
                    {props.user_display_settings[UserAnimeDisplaySettingsSchema.rating] ?
                        <TableCell className="color-gray" align="center">Rating</TableCell> : null}
                    {props.user_display_settings[UserAnimeDisplaySettingsSchema.genres] ?
                        <TableCell className="color-gray" align="center">Genres</TableCell> : null}
                    {props.user_display_settings[UserAnimeDisplaySettingsSchema.mal_score] ?
                        <TableCell className="color-gray" align="center">Score</TableCell> : null}
                    {props.user_display_settings[UserAnimeDisplaySettingsSchema.aired] ?
                        <TableCell className="color-gray" align="center">Aired</TableCell> : null}
                </TableRow>
            </TableHead>;
        case BRANCH_MANGA:
            return <TableHead>
                <TableRow>
                    <TableCell className="color-gray" align="center">#</TableCell>
                    <TableCell className="color-gray" align="center"><FontAwesomeIcon icon={faImage}/></TableCell>
                    <TableCell className="color-gray">Title</TableCell>
                    <TableCell className="color-gray" align="center"><FontAwesomeIcon icon={faEdit}/></TableCell>
                    {props.user_display_settings[UserMangaDisplaySettingsSchema.type] ?
                        <TableCell className="color-gray" align="center">Type</TableCell> : null}
                    {props.user_display_settings[UserMangaDisplaySettingsSchema.volumes] ?
                        <TableCell className="color-gray" align="center">Volumes</TableCell> : null}
                    {props.user_display_settings[UserMangaDisplaySettingsSchema.chapters] ?
                        <TableCell className="color-gray" align="center">Chapters</TableCell> : null}
                    {props.user_display_settings[UserMangaDisplaySettingsSchema.genres] ?
                        <TableCell className="color-gray" align="center">Genres</TableCell> : null}
                    {props.user_display_settings[UserMangaDisplaySettingsSchema.mal_score] ?
                        <TableCell className="color-gray" align="center">Score</TableCell> : null}
                    {props.user_display_settings[UserMangaDisplaySettingsSchema.aired] ?
                        <TableCell className="color-gray" align="center">Published</TableCell> : null}
                </TableRow>
            </TableHead>;
        case BRANCH_EROGE:
            return <TableHead>
                <TableRow>
                    <TableCell className="color-gray" align="center">#</TableCell>
                    <TableCell className="color-gray" align="center"><FontAwesomeIcon icon={faImage}/></TableCell>
                    <TableCell className="color-gray">Title</TableCell>
                    {props.user_display_settings[UserErogeDisplaySettingsSchema.type] ?
                        <TableCell align="center">Type</TableCell> : null}
                    {props.user_display_settings[UserErogeDisplaySettingsSchema.censorship] ?
                        <TableCell align="center">Censorship</TableCell> : null}
                    {props.user_display_settings[UserErogeDisplaySettingsSchema.language] ?
                        <TableCell align="center">Language</TableCell> : null}
                    {props.user_display_settings[UserErogeDisplaySettingsSchema.platforms] ?
                        <TableCell align="center">Platform</TableCell> : null}
                    {props.user_display_settings[UserErogeDisplaySettingsSchema.tags] ?
                        <TableCell align="center">Tags</TableCell> : null}
                    {props.user_display_settings[UserErogeDisplaySettingsSchema.hg_score] ?
                        <TableCell align="center">Score</TableCell> : null}
                </TableRow>
            </TableHead>;
        default:
            return null
    }
}